@import "sass/varialble";
@import "sass/function";

.container__cool-all {}

.cool-all {
  .layout {
    @include tabletSmall {
      flex-direction: column; } } }

.cool-all__action-block {
  margin-top: 53px;
  @include desktopSmall {
    margin-top: 19px; }
  @include tabletSmall {
    margin-top: 45px; } }

.action-block {
  width: 265px;
  height: 255px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  @include desktopSmall {
    width: 160px;
    height: 269px; }
  @include tabletSmall {
    width: 213px;
    height: auto;
    margin: 0 auto; } }

.action-block__heading {
  @include fontFSC($goth-m, 24px, $clr);
  text-align: center;
  line-height: 40px;
  @include desktopSmall {
    font-size: 18px;
    line-height: 24px; } }

.action-block__description {
  @include fontFSC($goth-r, 16px, $clr);
  text-align: center;
  margin-top: 15px;
  flex-grow: 5;
  line-height: 24px;
  img {
    margin-top: 9px; }
  @include desktopSmall {
    font-size: 14px;
    line-height: 20px;
    img {
      display: none; } }
  @include tabletSmall {
    height: auto; } }

.action-block__button {
  margin: 0 auto;
  @include desktopSmall {
    width: 160px;
    height: 34px;
    background-color: transparent; }
  @include tabletSmall {
    margin-top: 25px; } }
